import { app, BrowserWindow } from "electron";

export const mainMenuTemplate = {
    label: 'Main',
    submenu: [{
        label: 'On Top',
        accelerator: 'CmdOrCtrl+T',
        type: 'checkbox',
        checked: true,
        click: function (item, BrowserWindow) {
            mainWindow.setAlwaysOnTop(item.checked);
            contextMenu.items[1].checked = item.checked;
            trayIcon.setContextMenu(contextMenu);
        }
    }, {
        type: 'separator'
    }, {
        label: 'Minimize to Tray ',
        accelerator: 'CmdOrCtrl+Z',
        type: 'checkbox',
        checked: true
    }, {
        label: 'Close to Tray ',
        type: 'checkbox',
        checked: true
    }, {
        type: 'separator'
    }, {
        label: 'Auto-Hide Menu Bar',
        type: 'checkbox',
        checked: false,
        click: function (item, BrowserWindow) {
            mainWindow.setAutoHideMenuBar(item.checked);
            mainWindow.setMenuBarVisibility(!item.checked);
            contextMenu.items[3].checked = item.checked;
            trayIcon.setContextMenu(contextMenu);
        }
    }, {
        type: 'separator'
    }, {
        label: 'Quit',
        accelerator: 'CmdOrCtrl+Q',
        click: function (item, BrowserWindow) {
            if (mainWindow) {
                mainWindow._events.close = null; // Unreference function show that App can close
                app.quit();
            }
        }
    },
    ]};


